import { Component } from 'react';

class LangText extends Component {
  constructor(props) {
    super(props);
    let texts = {
        'login_text': {
          'eng': 'To start work you need to join or create your session',
          'rus': 'Чтобы начать работу нужно создать новую сессию или присоединиться к существующей'
        },
        'email_text' : {
          'eng': 'Enter email',
          'rus': 'Введите Email'
        },      
        'password' : {
          'eng': 'Password',
          'rus': 'Пароль'
        },
        'role' : {
          'eng': 'Role',
          'rus': 'Роль'
        },   
        'cam1' : {
          'eng': 'First camera',
          'rus': 'Первая камера'
        },
        'cam2' : {
          'eng': 'Second camera',
          'rus': 'Вторая камера'
        },    
        'cam3' : {
          'eng': 'Third camera',
          'rus': 'Третья камера'
        }, 
        'director' : {
          'eng': 'Director',
          'rus': 'Режиссер'
        },    
        'language' : {
          'eng': 'Language',
          'rus': 'Язык'
        },
        'create_ses' : {
          'eng': 'Create session',
          'rus': 'Создать сессию'
        },
        'enter_ses' : {
          'eng': 'Enter session',
          'rus': 'Войти'
        },    
        'exit_ses' : {
          'eng': 'Exit session',
          'rus': 'Выйти из сессии'
        },
        'changing' : {
          'eng': 'changing',
          'rus': 'смена плана'
        },    
        'cam_text' : {
          'eng': 'Select current Shot',
          'rus': 'Выберите съемочный план'
        }    
    };
    this.state = { out: texts[props.str][props.lang] || props.str }
  }
 
  render() {
    return this.state.out;
  }  
    
}

export default LangText;


